.navbar {
  width: 100%;
  height: 15vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.left {
  height: 60%;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25vh;
}
.right {
  height: 60%;
  width: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.scroll_trend {
  /* width: 25px;
    height: 40px;
    border-radius: 50px; */
  /* border: 2px solid #fff; */
  color: #fff;
  font-size: 30px;
  /* box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset; */
  /* box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px; */
}
.scroll_trend:hover {
  transform: scale(1.1);
  transition: all 0.6s;
}
.logo {
  height: 100%;
}
.mid {
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60%;
}
@media screen and (max-width: 1024px) {
  .mid {
    width: 60%;
  }
  input::placeholder {
    font-size: 18px;
  }
  .searchList {
    width: 57%;
    top: 70px;
  }
  .trending {
    height: 270vh;
  }
  .trending-conatiner{
    width: 95%;
  }
  .row{
    width: 100%;
    height: 400px;
  }

}
@media screen and (max-width: 768px) {
  .mid {
    width: 50%;
  }
  h1 {
    font-size: 70px;
  }
  .h3 {
    font-size: 30px;
  }
  .searchList {
    width: 45%;
    top: 70px;
  }

}
@media screen and (max-width: 630px) {
  .h3 {
    font-size: 25px;
  }
  .fa-search {
    font-size: 16px;
  }
  input::placeholder {
    font-size: 14px;
  }

  .logo {
    width: 70%;
  }
  .right {
    width: 20%;
  }
  .github_btn {
    font-size: 30px;
  }
  .search {
    font-size: 18px;
  }
}
.navbar-mobile {
  display: none;
}
@media screen and (max-width: 426px) {
  h1 {
    font-size: 45px;
  }
  .h3 {
    font-size: 25px;
    width: 80%;
  }
  .scroll_trend {
    font-size: 25px;
  }
  .navbar-mobile {
    display: flex;
    width: 100%;
    height: 50px;
    justify-content: space-between;
    align-items: center;
  }
  .left,
  .right {
    display: none;
  }
  .mid {
    width: 85%;
  }
  .searchList {
    width: 80%;
    top: 65px;
  }
}
@media screen and (max-width: 321px) {
  h1 {
    font-size: 38px;
  }
  .h3 {
    font-size: 23px;
  }
}
